




















































import {getCarCardList,changeChoose,deleteCard} from 'common/js/requestUrl'
import {request,showToast} from 'common/js/common'
import card from 'static/img/icon/card.png'
import deleteIcon from 'static/img/icon/delete.png'
export default {
	data() {
		return {
			card,
			deleteIcon,
			usingCard: '暂无车牌',
			carcardList: [],
			choosedCard: {}
		};
	},
	onShow(){
		this.getCardList()
	},
	methods:{
		navAddCard(){
			uni.navigateTo({
				url: '../addCard/addCard'
			});
		},
		getCardList(){
			this.carcardList = []
			this.choosedCard = {}
			request(getCarCardList,{},(res) => {
					if(res.result.data != null){
						res.result.data.forEach( v => {
							if(v.choosed == 1){
								this.usingCard = v.prefix + v.address_code+'·'+v.card
							}else{
								this.carcardList.push(v)
							}
						})
					}
					// this.carcardList = res.result.data
				}
			)
		},
		changeCarChoosed(id, choosed){
			if(choosed != 1){
				request(changeChoose,{
					id: id
				},(res) => {
					this.getCardList()
				})
			}
		},
		deleteCard(id){
			uni.showModal({
				title: '提示',
				content: '是否删除该车牌',
			success: (res) => {
				if (res.confirm) {
					request(deleteCard,
							{id},
							(res) => {
							if(res.code == 200){
								showToast('删除成功')
								this.getCardList()
							}
						}
					)}
				}
			});
		}
	}
}
